

@media (orientation: landscape) {
    .Euterpe {
        text-align: center;
        background-color: black;
        height: 100vh;
        display: flex;
        flex-direction: row;
    }
    
    .Euterpe .CellularAutomataSketch {
        border: 5px solid darkslategray;
        width: 75%;
        box-sizing: content-box;
    }
    
    .Euterpe .Panel {
        background-color: blanchedalmond;
        flex-grow: 1;
        width: 25%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }
    
    .Euterpe .Headers {
        align-self: flex-start;
        width: 100%;
        white-space: nowrap;
        font-size: 1vw;
    }
    
    .Euterpe .EarthRadioLogo {
        margin: 0 auto;
        margin-top: 10%;
        width: 25%;
        height: auto;
    }
    
    .Euterpe .Title {
        color: darkslategrey;
        margin: 0 auto;
    }
    
    .Euterpe .Subtitle {
        color: black;
        margin: 0 auto;
        margin-left: 30%;
    }
    
    .Euterpe .Controllers {
        margin-block-end: 100%;
        padding: 10px;
    }
    
    .Euterpe .PlayingRule {
        color: black;
        font-size:2vmin;
    }
    
    .Euterpe .ruleButton {
        width: 75%;
        color: black;
        font: inherit;
        padding: 10px;
        font-size:2vmin;
        background-color: grey;
    }
    
    .Euterpe .startButton {
        width: 75%;
        color: black;
        font: inherit;
        padding: 10px;
        font-size:2vmin;
        background-color: grey;
    }
}

@media (orientation: portrait) {
    .Euterpe {
        text-align: center;
        background-color: black;
        height: 100%;
        width: 100vw;
        display: flex;
        flex-direction: column;
    }
    
    .Euterpe .CellularAutomataSketch {
        border: 5px solid darkslategray;
        background-color: black ;
        height: 80vh;
        box-sizing: content-box;
    }
    
    .Euterpe .Panel {
        background-color: blanchedalmond;
        flex-grow: 1;
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
    }
    
    .Euterpe .Headers {
        align-self: center;
        width: fit-content;
        height: fit-content;
        white-space: nowrap;
        margin-left: 25%;
        font-size: 1vw;
    }
    
    .Euterpe .EarthRadioLogo {
        margin: 0 auto;
        margin-top: 10%;
        width: 50%;
        height: auto;
        align-self: center;
    }
    
    .Euterpe .Title {
        color: darkslategrey;
        margin: 0 auto;
    }
    
    .Euterpe .Subtitle {
        color: black;
        margin: 0 auto;
        margin-left: 30%;
    }
    
    .Euterpe .Controllers {
        padding: 10px;
        margin-right: 25%;
        align-self: center;
    }
    
    .Euterpe .PlayingRule {
        color: black;
        font-size:2vmin;
    }
    
    .Euterpe .ruleButton {
        width: 100%;
        color: black;
        font: inherit;
        padding: 10px;
        font-size:2vmin;
        background-color: grey;
    }
    
    .Euterpe .startButton {
        width: 100%;
        color: black;
        font: inherit;
        padding: 10px;
        font-size:2vmin;
        background-color: grey;
    }
}